import React, { useState, useEffect } from 'react'
import './Form.scss'

function Form() {

    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [message, setMessage] = useState('')


    return (
        <div id='contact' className='form'>
            <div className='form-intro'>
                <h1>Write us a letter</h1>
                <p>We appreciate you ideas</p>
                <form>
                    <label>NAME</label>
                    <input value={name} onChange={e => setName(e.target.value)} type='text' placeholder='Enter your name'/>
                    <label>EMAIL ADDRESS</label>
                    <input value={email} onChange={e => setEmail(e.target.value)} type='text' placeholder='Enter your email address'/>
                    <label>YOUR MESSAGE</label>
                    <input value={message} onChange={e => setMessage(e.target.value)} type='text' placeholder='Example text'/>
                    <button type='submit'>Submit</button>
                </form>
            </div>
        </div>
    )
}
export default Form
